//переходю сюди з MoviesList та рендеримо один фільм
//з цієї сторінки переходю на cast and reviews

import { Loader } from 'components/Loader/Loader';
import { useGetMovieDetails } from 'hooks';
import { Suspense, useRef } from 'react'; //useRef повернення до Home
import { Outlet, useLocation, Link } from 'react-router-dom';
import {
  Button,
  Img,
  P,
  Ul,
  Li,
  Links,
  None,
  Div,
  Title,
} from './MovieDetails.styled';

 const MovieDetails = () => {
  const { movie, error, isLoading } = useGetMovieDetails();

  const location = useLocation();
  const backLinkHref = useRef(location?.state?.from ?? '/'); //Go back повернення до Home to={backLinkHref.current}
  //   console.log(movie);
  return (
    <>
      <Link to={backLinkHref.current}>
        <Button>Go back</Button>
      </Link>

      {movie && (
        <div>
          <Div>
            <div>
              <Img
                src={
                  movie.poster_path
                    ? `https://image.tmdb.org/t/p/w300${movie.poster_path}`
                    : `data:image/jpeg;base64,`
                }
                alt={movie.title}
              />
            </div>
            <div>
              <Title>{movie.title || movie.original_title}</Title>
              <P>Rating: {movie.vote_average}</P>
              <P>{movie.overview}</P>

              <Ul>
                {movie.genres &&
                  movie.genres.map(({ id, name }) => <Li key={id}>{name}</Li>)}
              </Ul>
            </div>
          </Div>
          <None>
            <li>
              <Links to="cast">Cast</Links>
            </li>
            <li>
              <Links to="reviews">Reviews</Links>
            </li>
          </None>
          {isLoading && <Loader />}
          {error && <p>Something went wrong</p>}
          <Suspense fallback={<Loader />}>
            <Outlet />
          </Suspense>
        </div>
      )}
    </>
  );
};
export default MovieDetails;